import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { ConfirmationService, MessageService, SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { DespesaDTO } from 'src/app/shared/interfaces/despesas/DespesaDTO';
import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { DespesaService } from 'src/app/shared/services/despesa/despesa.service';
import { DespesaCadastroComponent } from '../despesa-cadastro/despesa-cadastro.component';
import { DespesaDetailComponent } from '../despesa-detail/despesa-detail.component';

@Component({
  selector: 'app-despesa-main',
  templateUrl: './despesa-main.component.html',
  styleUrls: ['./despesa-main.component.scss'],
})
export class DespesaMainComponent implements OnInit, OnDestroy {

  despesas: DespesaDTO[] = [];
  sortOptions: SelectItem[] = [];

  isLoaded = false;

  private subscriptions = new Subscription();

  constructor(
    private despesaService: DespesaService,
    private modal: ModalController,
    private toastService: MessageService,
    private storage: StorageService,
    private confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    this.loadDespesas();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadDespesas() {
    const sub =  this.despesaService.list()
      .subscribe(data => {
        this.despesas = data;
        this.isLoaded = true;
      });

      this.subscriptions.add(sub);
  }

  changeFiltro(event) {
    const { value } = event;
    this.loadDespesas();
  }

  createNewDespesa() {
    this.showModal(DespesaCadastroComponent);
  }

  editarDespesa(despesa: DespesaDTO) {
    this.showModal(DespesaDetailComponent, { despesa });
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss()
      .then((data) => {
        const { data: hasUpdate } = data;
        if (hasUpdate) {
          this.loadDespesas();
          this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
        }
      });

    return await modal.present();
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
