import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';

@Component({
  selector: 'app-loterica-detail',
  templateUrl: './loterica-detail.component.html',
  styleUrls: ['./loterica-detail.component.scss'],
})
export class LotericaDetailComponent implements OnInit, OnDestroy {

  @Input()
  loterica: LotericaDTO;

  form: FormGroup;
  isLoaded = false;

  wasCreated = false;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private lotericaService: LotericaService,
    private modal: ModalController,

  ) {
  }

  ionViewWillEnter() {
  }

  ngOnInit() {

    const { nome, descricao, codigo } = this.loterica;

    this.form = this.formBuilder.group({
      nome: [nome, [Validators.required]],
      descricao: [descricao, [Validators.required,]],
      codigo: [codigo, [Validators.required,]],
    });

  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  update() {
    const { id } = this.loterica;

    const nome = this.form.get('nome').value;
    const descricao = this.form.get('descricao').value;
    const codigo = this.form.get('codigo').value;

    const sub = this.lotericaService.updateLoterica({ nome, descricao, codigo }, id)
      .subscribe(response => {
        this.fechar(true)
      });
      
      this.subscriptions.add(sub);
  }

  fechar(wasCreated = false) {
    this.modal.dismiss(wasCreated);
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
