import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';

@Component({
  selector: 'app-loterica-cadastro',
  templateUrl: './loterica-cadastro.component.html',
  styleUrls: ['./loterica-cadastro.component.scss'],
})
export class LotericaCadastroComponent implements OnInit, OnDestroy {
    
  form: FormGroup;
  isLoaded = false;

  steps: MenuItem[];
  stepIndex = 0;

  createdLotericaId = '';
  wasCreated = false;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private lotericaService: LotericaService,
    private modal: ModalController,

  ) {

    this.form = this.formBuilder.group({
      nome: [, [Validators.required]],
      descricao: [, [Validators.required,]],
      codigo: [, [Validators.required,]],
    });
  }

  ionViewWillEnter() {
  }

  ngOnInit() {
    this.steps = [
      {
        label: 'Informações',
      },
      {
        label: 'Endereço',
      },
    ];
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  create() {
    const nome = this.form.get('nome').value;
    const descricao = this.form.get('descricao').value;
    const codigo = this.form.get('codigo').value;
    
    const sub = this.lotericaService.createLoterica({ nome, descricao, codigo})
      .subscribe(response => {
        this.createdLotericaId = response.id;
        this.wasCreated = true;
        this.stepIndex++;
      });

      this.subscriptions.add(sub);
  }


  listenEndereco(event) {
    this.fechar(this.wasCreated);
  }

  fechar(wasCreated = false) {
    this.modal.dismiss(this.wasCreated);
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
