import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { ConfirmationService, MessageService, SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { ProdutoDTO } from 'src/app/shared/interfaces/produtos/ProdutoDTO';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { ProdutoService } from 'src/app/shared/services/produto/produto.service';
import { ProdutoCadastroComponent } from '../produto-cadastro/produto-cadastro.component';
import { ProdutoDetailComponent } from '../produto-detail/produto-detail.component';

@Component({
  selector: 'app-produto-main',
  templateUrl: './produto-main.component.html',
  styleUrls: ['./produto-main.component.scss'],
})
export class ProdutoMainComponent implements OnInit, OnDestroy {

  produtos: ProdutoDTO[] = [];
  sortOptions: SelectItem[] = [];

  isLoaded = false;

  private subscriptions = new Subscription();

  constructor(
    private produtoService: ProdutoService,
    private modal: ModalController,
    private toastService: MessageService,
    private storage: StorageService,
    private confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    this.loadProdutos();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadProdutos() {
    const sub = this.produtoService.list()
      .subscribe(data => {
        this.produtos = data;
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  changeFiltro(event) {
    const { value } = event;
    this.loadProdutos();
  }

  createNewProduto() {
    this.showModal(ProdutoCadastroComponent);
  }

  editarProduto(produto: ProdutoDTO) {
    this.showModal(ProdutoDetailComponent, { produto });
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss()
      .then((data) => {
        const { data: hasUpdate } = data;
        if (hasUpdate) {
          this.loadProdutos();
          this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
        }
      });

    return await modal.present();
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
