import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { ProdutoDTO } from 'src/app/shared/interfaces/produtos/ProdutoDTO';
import { ProdutoService } from 'src/app/shared/services/produto/produto.service';

@Component({
  selector: 'app-produto-detail',
  templateUrl: './produto-detail.component.html',
  styleUrls: ['./produto-detail.component.scss'],
})
export class ProdutoDetailComponent implements OnInit, OnDestroy {

  @Input()
  produto: ProdutoDTO;

  form: FormGroup;
  isLoaded = false;

  createdprodutoId = '';
  wasCreated = false;

  
  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private produtoService: ProdutoService,
    private modal: ModalController,
  ) {
  }

  ngOnInit() {
    const { nome, descricao, preco_unitario } = this.produto;

    this.form = this.formBuilder.group({
      nome: [nome, [Validators.required]],
      descricao: [descricao, [Validators.required,]],
      preco_unitario: [preco_unitario, [Validators.required,]],
    });
  }
  
  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  update() {
    const { id } = this.produto;

    const nome = this.form.get('nome').value;
    const descricao = this.form.get('descricao').value;
    const preco_unitario = this.form.get('preco_unitario').value;

    const sub = this.produtoService.update({ nome, descricao, preco_unitario }, id)
      .subscribe(response => {
        this.fechar(true);
      });

      this.subscriptions.add(sub);
  }
  
  fechar(wasCreated = false) {
    this.modal.dismiss(wasCreated);
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
