import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { MessageService, SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { EnderecoComponent } from '../../../endereco-module/endereco/endereco.component';
import { UpdateEnderecoComponent } from '../../../endereco-module/update-endereco/update-endereco.component';
import { LotericaCadastroComponent } from '../loterica-cadastro/loterica-cadastro.component';
import { LotericaDetailComponent } from '../loterica-detail/loterica-detail.component';

@Component({
  selector: 'app-loterica-main',
  templateUrl: './loterica-main.component.html',
  styleUrls: ['./loterica-main.component.scss'],
})
export class LotericaMainComponent implements OnInit, OnDestroy {

  lotericas: LotericaDTO[] = [];
  sortOptions: SelectItem[] = [];

  isLoaded = false;

  private subscriptions = new Subscription();

  constructor(
    private lotericaService: LotericaService,
    private modal: ModalController,
    private toastService: MessageService,
  ) { }

  ngOnInit() {
    this.loadLotericas();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadLotericas() {
   const sub =  this.lotericaService.list()
      .subscribe(data => {
        this.lotericas = data;
        this.isLoaded = true;
      });

      this.subscriptions.add(sub);
  }

  lotericaLabel(loterica: LotericaDTO) {
    return `${loterica.nome} - ${loterica.codigo}`;
  }

  changeFiltro(event) {
    const { value } = event;
    this.loadLotericas();
  }

  createNewLoterica() {
    this.showModal(LotericaCadastroComponent);
  }

  editarLoterica(loterica: LotericaDTO) {
    //TODO - Implementar no APP e na API a atualização de Loterica
    this.showModal(LotericaDetailComponent, { loterica });
  }


  editarEndereco(loterica: LotericaDTO) {
    if (loterica.endereco) {
      this.showModal(UpdateEnderecoComponent, { currentEndereco: loterica, isFuncionario: false });
    } else {
      this.showModal(EnderecoComponent, { idOf: loterica.id, isFuncionario: false, isModal: true });
    }
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss()
      .then((data) => {
        const { data: hasUpdate } = data;
        if (hasUpdate) {
          this.loadLotericas();
          this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
        }
      });

    return await modal.present();
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
